*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #616161;
  font-family: "Calibri", arial, sans-serif;
  font-size: 18px;
  line-height: 25px;
  font-weight: normal;
  background: #ffffff url("../img/main-bg@1x.png") no-repeat;
  background-size: cover;
  background-position: center top 20px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body {
      background-image: url("../img/main-bg@2x.png"); } }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

.container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (max-width: 767px) {
    .container {
      padding: 0 20px; } }
  .container--small {
    max-width: 883px; }

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 0;
  flex-wrap: wrap; }

.header__logo {
  padding-right: 25px; }

.header__phone {
  padding-top: 10px;
  align-self: flex-start;
  font-weight: 700;
  color: #141419;
  padding-left: 25px; }
  @media (max-width: 767px) {
    .header__phone {
      padding: 0;
      align-self: center; } }

.nav {
  flex-grow: 1;
  max-width: 772px;
  margin-right: 25px; }
  @media (max-width: 1023px) {
    .nav {
      order: 2;
      width: 100%;
      margin: 20px auto 0; } }
  @media (max-width: 767px) {
    .nav--close .nav__list {
      height: 0;
      overflow: hidden; }
    .nav--close .nav__toggle::before {
      top: 4px;
      left: 50%;
      width: 20px;
      height: 2px;
      transform: translateX(-50%);
      box-shadow: 0 6px 0 0 #141419, 0 12px 0 0 #141419; }
    .nav--close .nav__toggle::after {
      content: none; } }

.nav__list {
  transition: height 0.5s;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none; }
  @media (max-width: 767px) {
    .nav__list {
      flex-direction: column; } }

.nav__list li {
  display: flex; }
  @media (max-width: 767px) {
    .nav__list li {
      border-bottom: 1px solid grey;
      margin: 0 -20px;
      padding: 5px 20px; }
      .nav__list li:first-child {
        border-top: 1px solid grey; } }

.nav__link {
  transition: 0.3s;
  padding: 5px;
  color: #616161;
  font-size: 18px;
  line-height: 29px; }
  .nav__link:hover, .nav__link:focus {
    opacity: 0.5;
    text-decoration: underline #fb973b; }
  @media (max-width: 767px) {
    .nav__link {
      font-size: 28px;
      line-height: 35px; } }

.nav__toggle {
  display: none; }
  @media (max-width: 767px) {
    .nav__toggle {
      position: absolute;
      top: 20px;
      right: 30px;
      display: block;
      width: 21px;
      height: 21px;
      padding: 0;
      cursor: pointer;
      border: none;
      background-color: transparent; }
      .nav__toggle::before, .nav__toggle::after {
        transition: 0.5s;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 28px;
        height: 3px;
        content: "";
        background-color: #000000; }
      .nav__toggle::before {
        transform: translate(-50%, -50%) rotate(45deg); }
      .nav__toggle::after {
        transform: translate(-50%, -50%) rotate(-45deg); } }

.title {
  color: #141419;
  font-size: 60px;
  line-height: 72px; }
  @media (max-width: 1023px) {
    .title {
      font-size: 50px;
      line-height: 60px; } }
  @media (max-width: 767px) {
    .title {
      font-size: 30px;
      line-height: 40px;
      text-align: center; } }

.title span {
  box-shadow: inset 0 0.75em #ffffff, inset 0 0.95em #fb973b; }

.subtitle {
  margin: 0 0 30px;
  font-size: 24px;
  line-height: 36px; }
  .subtitle b {
    color: #fb973b; }
  @media (max-width: 1023px) {
    .subtitle {
