.form input[type="text"],
.form input[type="tel"] {
  height: 50px;
  padding-left: 17px;
  transition: 0.3s;
  color: inherit;
  border: 1px solid #e1e1e1;
  border-radius: 0;
  background-color: #f8f8f8;
  box-sizing: border-box;
  font-family: "SFUIDisplay", arial, sans-serif; }
  .form input[type="text"]::placeholder,
  .form input[type="tel"]::placeholder {
    color: inherit; }
  .form input[type="text"]:focus, .form input[type="text"]:hover,
  .form input[type="tel"]:focus,
  .form input[type="tel"]:hover {
    background-color: #ffffff; }

.form button {
  height: 50px;
  cursor: pointer;
  box-sizing: inherit;
  transition: 0.3s;
  color: #ffffff;
  border: none;
  border-radius: 0;
  background-color: #fb973b;
  font-family: "SFUIDisplay", arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 31px; }
  .form button:hover, .form button:focus {
    opacity: 0.8; }
  .form button:active {
    opacity: 0.6; }

.promo {
  background: url("../img/promo-bg@1x.png") no-repeat;
