      width: 100%;
      margin-bottom: 20px; } }

.about-us__list {
  width: calc(50% - 15px);
  padding: 0;
  margin: 0;
  list-style: none;
  counter-reset: features;
  text-align: center; }
  @media (max-width: 767px) {
    .about-us__list {
      width: 100%; } }

.about-us__item {
  position: relative;
  padding: 50px 0 18px;
  counter-increment: features;
  color: #141419; }
  .about-us__item::before {
    position: absolute;
    top: 0;
    left: calc(50% - 11px);
    width: 22px;
    height: 22px;
    z-index: 1;
    content: counter(features);
    color: #fb973b;
    border: 1px solid #fb973b;
    border-radius: 50%;
    background-color: white;
    font-size: 18px;
    line-height: 18px; }
  .about-us__item::after {
    content: "";
    top: -20px;
    left: calc(50% + 0.5px);
    position: absolute;
    width: 1px;
    height: 60px;
    background-color: #fb973b; }
  .about-us__item:first-child::after {
    top: 0;
    height: 40px; }

